import React, {useState} from "react";

import {Form, Row, Col, Button, Container} from "react-bootstrap";
import FloatingLabel from "react-bootstrap-floating-label";
import Select from "react-select";
import ReCAPTCHA from "react-google-recaptcha";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";


function Forms() {
    function handleSubmit(e) {
        e.preventDefault();

        alert('Hàm xử lý validate thông tin');
    }

    const [type, setType] = useState('');
    const [typeMUA, setTypeMUA] = useState('');
    const [startDate, setStartDate] = useState(new Date());

    const myKey = '6Ld95AEmAAAAAJYMx8dGcXSC5-pamf5TuQ7qvCWu';

    const options_KhuVucSinhSong = [
        {value: "0", label: ''},
        {value: "1", label: 'Hồ chí Minh'},
        {value: "2", label: 'Hà Nội'},
        {value: "3", label: 'Đồng Nam'},
        {value: "4", label: 'Lào Cai'},
        {value: "5", label: 'Cà Mau'},
        {value: "6", label: 'Điện Biên'},
        {value: "7", label: 'Tây Ban Nha'}
    ]
    const options_MucDichMuaBH = [
        {value: "0", label: ''},
        {value: "1", label: 'Cho cá nhân'},
        {value: "2", label: 'Cho gia đình'},
        {value: "3", label: 'khác'},

    ]

    return (
        <Form onSubmit={handleSubmit} id="frm-dang-ky-tu-van" className="box_form">

            <Row className="no-gutters">
                <div className="col-12">
                    <Row className="row_custom no-gutters">
                        <Col lg="6">
                            <FloatingLabel
                                controlId="floatingInput"
                                label="Họ & tên"
                                className="mb-3"
                            >
                                <Form.Control id="name" className="form-control" name="name" type="text"
                                              placeholder="Họ & tên *"/>
                            </FloatingLabel>
                        </Col>
                        <Col lg="6">
                            <FloatingLabel className="mb-3" controlId="floatingSdt" label="Số điện thoại">
                                <Form.Control maxLength={10} name="phone" id="phone" type="text"
                                              placeholder="Số điện thoại *"/>
                            </FloatingLabel>
                        </Col>
                    </Row>

                    <Row className="row_custom no-gutters">
                        <Col lg="6">
                            <FloatingLabel
                                id="floatingEmail"
                                label="Email"
                                className="mb-3"
                            >
                                <Form.Control id="email" className="form-control" name="email" type="text"
                                              placeholder="Email *"/>
                            </FloatingLabel>
                        </Col>
                        <Col lg="6">
                            <Form.Group controlId="formBasicSelect" className="floating-label mb-1">
                                <Form.Label style={{marginBottom:"0.7666666666666667rem"}}>Khu vực sinh sống</Form.Label>
                                <Form.Control
                                    as="select"
                                    value={type}
                                    onChange={e => {
                                        setType(e.target.value);
                                    }}
                                >
                                    {options_KhuVucSinhSong.map((items, num) => {
                                        return <option value={items.value}>{items.label}</option>
                                    })}

                                </Form.Control>
                            </Form.Group>
                        </Col>

                    </Row>
                    <Row className="row_custom no-gutters">
                        <Col lg="6">
                            <Form.Group controlId="formBuybh" className="floating-label mb-mucdich mb-3">
                                <Form.Label   style={

                                    {marginBottom:"1rem"}
                                }>Mục đích mua bảo hiểm</Form.Label>
                                <Form.Control
                                    as="select"
                                    value={typeMUA}
                                    onChange={e => {
                                        setTypeMUA(e.target.value);
                                    }}
                                >
                                    {options_MucDichMuaBH.map((items, num) => {
                                        return <option value={items.value}>{items.label}</option>
                                    })}

                                </Form.Control>
                            </Form.Group>
                        </Col>
                        <Col lg="6">
                            <Form.Group controlId="formBirthday" className="floating-label mb-3">
                                <Form.Label
                                    style={{zIndex: 999,marginBottom:"0.7666666666666667rem"}}>Ngày tháng năm sinh</Form.Label>
                                <DatePicker id="formBirthday" className="form-control" name="birthday"
                                            showIcon
                                            selected={startDate}
                                            onChange={(date) => setStartDate(date)}
                                />
                            </Form.Group>

                        </Col>

                    </Row>

                    <Row className="no-gutters">
                        <Col lg="12">
                            <p className="txt_1">(<i className="text-danger">*</i>) Thông tin bắt buộc</p>
                        </Col>
                    </Row>

                    <Row className="no-gutters m-2">

                        <Col lg="12">
                            <div id="html-reCaptchaRD" className="text-center box_capcha">
                                <ReCAPTCHA
                                    sitekey={myKey}
                                />
                            </div>
                        </Col>
                    </Row>

                    <Row className="no-gutters m-2">
                        <Col lg="12">
                            <p className="txt_2 text-center">
                                Khi bấm vào nút <strong className="ql-color-blue">ĐĂNG KÝ TƯ VẤN</strong> đồng
                                nghĩa với việc bạn đã đọc qua và đồng ý với
                                <a href="#!">
                                    <u><b>Chính Sách Liên Hệ</b></u>
                                </a>
                                của chúng tôi.
                            </p>
                        </Col>
                    </Row>

                </div>

            </Row>


            <Row className="no-gutters text-center">
                <Col>
                    <Button type="submit" className="btn_dangky btn btn-primary">
                        <span>Đăng ký tư vấn</span>
                    </Button>
                </Col>
            </Row>

        </Form>
    );
}

export default Forms;