import React, {useState, useEffect, useRef} from "react";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import logo from '../assets/img/logo.webp';
import MediaQuery from 'react-responsive'

export default function Header() {
    const [toggle, setToggle] = useState(false);
    console.log(toggle);
    const isMenu = useRef();
    useEffect(() => {
        const handleOpenMenu = e => {
            if (e.srcElement != isMenu.current) {
                setToggle(false)
            }
        }
        document.body.addEventListener('click', handleOpenMenu)
        return () => document.body.removeEventListener('click', handleOpenMenu)
    }, [])

    return (
        <header id="header">
            <Navbar bg="light" expand="lg">
                <Container>
                    <Navbar.Brand href="#">
                        <img src={logo} alt="logo"/>
                    </Navbar.Brand>
                    <Navbar.Collapse id="navbarCollapse">
                        <Nav
                            className="navbar-nav ml-auto"
                            style={{maxHeight: '100px'}}
                            navbarScroll
                        >
                            <Nav.Link href="#dang-ky-tu-van" data-target="#dang-ky-tu-van">Home</Nav.Link>
                            <Nav.Link href="#gioi-thieu" data-target="#gioi-thieu">Giới thiệu</Nav.Link>
                            <Nav.Link href="#quyen-loi" data-target="#ql">Quyền lợi</Nav.Link>
                            <Nav.Link href="#uu-diem" data-target="#ud">Ưu điểm</Nav.Link>
                            <Nav.Link href="#quy-trinh" data-target="#qt">Quy Trình</Nav.Link>
                            <Nav.Link href="#hoi-dap" data-target="#ch">Hỏi/ đáp</Nav.Link>

                        </Nav>
                        <a className="navbar-btn btn ml-auto ml-lg-0 btn_header" href="#!">
                            <span>Đăng ký tư vấn</span>
                        </a>
                    </Navbar.Collapse>

                    <MediaQuery maxWidth={991}>
                        <button className="navbar-toggler" id="button-menu"
                                onClick={() => {
                                    setToggle(!toggle)
                                }}
                        >
                            <img ref={isMenu} src="https://www.ixdbiker.motorcycles/img/menu.webp?v=2086"/>
                        </button>

                        {

                            toggle &&
                            (
                                <div id="navbarCollapse" className="navbar-collapse collapse show"
                                >
                                    <div className="navbar-nav ml-auto">
                                        <div className="nav-item d-none">
                                            <a className="nav-link" data-target="#dang-ky-tu-van"
                                               href="#dang-ky-tu-van"></a>
                                        </div>
                                        <div className="nav-item">
                                            <a className="nav-link  " data-target="#gioi-thieu"
                                               href="#gioi-thieu">
                                                Giới thiệu
                                            </a>
                                        </div>
                                        <div className="nav-item">
                                            <a className="nav-link" data-target="#quyen-loi"
                                               href="#quyen-loi">
                                                Quyền lợi
                                            </a>
                                        </div>
                                        <div className="nav-item">
                                            <a className="nav-link" data-target="#uu-diem"
                                               href="#uu-diem">
                                                Ưu điểm
                                            </a>
                                        </div>
                                        <div className="nav-item">
                                            <a className="nav-link" data-target="#quy-trinh"
                                               href="#quy-trinh">
                                                Quy trình
                                            </a>
                                        </div>
                                        <div className="nav-item">
                                            <a className="nav-link" data-target="#hoi-dap"
                                               href="#hoi-dap">
                                                Hỏi / đáp
                                            </a>
                                        </div>
                                    </div>
                                    <div className="head_social mb">
                                        <div className="foot_social d-flex justify-content-end">
                                            <nav className="nav">
                                                <a className="nav-link" href="#!"
                                                   target="_blank"><i className="fab fa-facebook-f"></i></a>
                                            </nav>
                                        </div>
                                    </div>
                                </div>
                            )
                        }

                        <a className="navbar-btn btn ml-auto ml-lg-0 btn_header" href="#!">
                            <span>Đăng ký tư vấn</span>
                        </a>
                    </MediaQuery>
                </Container>
            </Navbar>
        </header>
    );
}

