import React, {useRef, useState} from "react";
import Slider from "react-slick";


const ItemListBind = (props) => <div className="item">
    <div className="procedure_info">
        <div className="d-flex flex-row align-items-center">
            <div className="txt-num">{props.num}</div>
            <div className="txt-name ml-3">
                <div className="title_txt"><p>{props.title}</p></div>
                <div className="desc_txt"><p>{props.desc}</p>
                    {props.link && (
                   <p><a href={props.link} target="_blank">(Danh sách bệnh viện liên kết)</a></p>)}
                </div>
            </div>
        </div>
    </div>
</div>

export default function ThuTuc() {
    const [nav1, setNav1] = useState();
    const [nav2, setNav2] = useState();
    const [currentPage, setCurrentPage] = useState(1);

    const gotoNext = () => {
        nav1.slickNext();
        currentPage < 3 && setCurrentPage(currentPage + 1);
    }
    const gotoPrev = () => {
        nav1.slickPrev();
        currentPage > 1 && setCurrentPage(currentPage - 1);
    }
    const SlickArrowLeft = () => <div onClick={gotoPrev} className="slick-prev slick-arrow"><i
        className="fas fa-chevron-left"></i></div>


    const SlickArrowRight = () => <div onClick={gotoNext} className="slick-next slick-arrow"><i
        className="fas fa-chevron-right"></i></div>

    const settings = {
        dots: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        initialSlide: 0,
        autoplay: false,
        arrows: true,
        slide:"item",
        customPaging: function (i) {
            return <>
                <div className="tu">{i + 1}</div>
                <div className="line"></div>
                <div className="mau">3</div>
            </>;
        },
        dotsClass: "pagination procedure_paging list-unstyled mb-0",
        prevArrow: <SlickArrowLeft/>,
        nextArrow: <SlickArrowRight/>,

    };

    const settings2 = {
        dots: false,
        speed: 500,slide:"item",
        slidesToShow: 1,
        slidesToScroll: 1,
        initialSlide: 0,
        autoplay: false,
        arrows: false,
    }

    return (
        <div className="wp d-lg-flex flex-row justify-content-center align-items-center">
            <div className="wp-form right">
                <div className="procedure_slider">

                    <Slider data-slider="1" asNavFor={nav2} ref={(slider1) => setNav1(slider1)} {...settings}
                            className="procedure_for style_arrow">
                        <div className="item">
                            <div className="procedure_img"><img src={require("../assets/img/b1.png")} alt="BAO VIET"/>
                            </div>
                        </div>

                        <div className="item">
                            <div className="procedure_img"><img src={require("../assets/img/b2.png")} alt="BAO VIET"/>
                            </div>
                        </div>
                        <div className="item">
                            <div className="procedure_img"><img src={require("../assets/img/b3.png")} alt="BAO VIET"/>
                            </div>
                        </div>
                    </Slider>


                    <Slider
                        asNavFor={nav1}
                        ref={(slider2) => setNav2(slider2)}
                        slidesToShow={1}
                        swipeToSlide={true}
                        focusOnSelect={true}
                        {...settings2} className="procedure_nav   slick-slider">

                        <ItemListBind
                            num="1"
                            title="Đảm bảo tài chính vững vàng trước rủi ro."
                            desc="Giảm bớt áp lực tài chính khi biến cố xảy ra, giúp bạn an tâm điều trị."
                        />
                        <ItemListBind
                            num="2"
                            title="Dịch vụ bảo lãnh viện phí rộng khắp Việt Nam."
                            desc="Tùy chọn bệnh viện tư nhân và quốc tế trên toàn quốc mà bạn tin tưởng."
                            link="//drive.google.com/file/d/10bVHa5_dBANPeRxau65Ve36D5JqVjliQ/view"
                        />
                        <ItemListBind
                            num="3"
                            title="Mua nhanh đơn giản, không cần khám sức khỏe."
                            desc="Đăng ký nhanh gọn chỉ trong vòng 5 phút với phí chỉ từ 3.000 đồng/ngày."
                        />
                    </Slider>
                </div>

            </div>
        </div>
    )
}