import Slider from "react-slick";
import React, {useRef} from "react";
import {useMediaQuery} from 'react-responsive'

const data = [
    {
        "id": "1323",
        "title": "Đồng",
        "content": "1.196.000",
        "description": " 93.800.000 "
    }, {
        "id": "424",
        "title": "Bạc",
        "content": "1.716.000",
        "description": " 137.600.000 "
    },
    {
        "id": "1",
        "title": "Vàng",
        "content": "2.600.000",
        "description": " 230.000.000 "
    }, {
        "id": "3123",
        "title": "Bạch kim",
        "content": "3.510.000",
        "description": " 342.000.000 "
    }, {
        "id": "43242",
        "title": "Kim cương",
        "content": "4.290.000",
        "description": " 454.000.000 "
    }

];

function BpItems(items) {
    const isMobile = useMediaQuery({query: '(max-width: 991px)'})
    items = items.items;
    let active = isMobile ? 'card item active ' : items.id == 1 ? 'card item active ' : 'card item';

    return (
        <div className={active}>
            <div className="card-body">
                <div className="card-text">
                    <div className="txt-h">
                        {items.title}
                    </div>
                    <div className="title_txt">
                        Chỉ từ <strong>{items.content}</strong>
                        <br/>
                        đồng/người/năm
                    </div>
                    <div className="l-check d-flex flex-row justify-content-center">
                        <div className="check">
                            <i className="fa fa-check" aria-hidden="true"></i>
                        </div>
                        <div className="title_txt color-p">
                            <p>Tổng giá trị bồi thường lên đến <b>{items.description}</b>đồng/người/năm</p>
                        </div>
                    </div>
                </div>
                <div className="bp-btn btn ">
                    <a type="submit" href="#!" className="bp-btn_dangky btn">
                        <span>Đăng ký tư vấn</span>
                    </a>
                </div>
            </div>

        </div>
    )
}

export default function BpSlider(proj) {
    const MB = () => {
        const customeSlider = useRef();

        const gotoNext = () => {
            customeSlider.current.slickNext()


        }
        const gotoPrev = () => {
            customeSlider.current.slickPrev()

        }
        const SlickArrowLeft = () => (
            <div onClick={gotoPrev} className="slick-prev slick-arrow"><i
                className="fas fa-chevron-left"></i></div>
        );


        const SlickArrowRight = () => (
            <div onClick={gotoNext} className="slick-next slick-arrow"><i
                className="fas fa-chevron-right"></i></div>
        );

        const settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: proj.isMobile ? 1 : 4,
            slidesToScroll: 1,
            initialSlide: 0,
            autoplay: true,
            arrows: true,
            prevArrow: <SlickArrowLeft/>,
            nextArrow: <SlickArrowRight/>,

        };


        return (
            <Slider ref={customeSlider}
                    className="slick-mobile style_dot style_arrow d-lg-flex flex-row justify-content-center" {...settings}>
                {

                    data.map((items, index) => {
                        return <BpItems items={items} key={index}/>
                    })

                }
            </Slider>
        )
    }

    const PC = () => {
        return (
            <div className="style_dot style_arrow d-lg-flex flex-row justify-content-center">
                {
                    data.map((items, index) => {
                        return <BpItems items={items} key={index}/>
                    })
                }
            </div>
        )
    }


    return proj.isMobile ? <MB/> : <PC/>
}