import Slider from "react-slick";
import React, {useRef} from "react";

function ProcedureItem(image) {
    return (
        <div className="item">
            <div className="procedure_img">
                <img className="pc"
                     src={image.image}
                     alt="BAO VIET"/>
            </div>
        </div>
    )
}

function ProcedureNav(proj) {
    return (
        <div className="item">
            <div className="procedure_info">
                <div className="d-flex flex-row align-items-center">
                    <div className="txt-num">{proj.name}</div>
                    <div className="txt-name ml-3">
                        <div className="title_txt">{proj.description}</div>
                        <div className="desc_txt">{proj.content}</div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default function ProcedureSlider() {
    const nav1 = useRef()
    const gotoNext = () => {
        nav1.current.slickNext();
    }
    const gotoPrev = () => {
        nav1.current.slickPrev();
    }

    const SlickArrowLeft = () => <div onClick={gotoPrev} className="slick-prev slick-arrow"><i
        className="fas fa-chevron-left"></i></div>


    const SlickArrowRight = () => <div onClick={gotoNext} className="slick-next slick-arrow"><i
        className="fas fa-chevron-right"></i></div>


    const settings = {
        autoplaySpeed: 4000,
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        initialSlide: 0,
        autoplay: false,
        arrows: true,
        customPaging: function (i) {
            return <><div className="tu">{i + 1}</div><div className="line"></div><div className="mau">3</div></>;
        },
        dotsClass: "pagination procedure_paging list-unstyled mb-0",
        prevArrow: <SlickArrowLeft/>,
        nextArrow: <SlickArrowRight/>,
    };


    return (
        <Slider ref={nav1} {...settings}>
            <ProcedureItem
                image={require("../../assets/img/b1.png")}></ProcedureItem>
            <ProcedureItem
                image={require("../../assets/img/b2.png")}></ProcedureItem>
            <ProcedureItem
                image={require("../../assets/img/b3.png")}></ProcedureItem>
        </Slider>

    )
}