import React, {useState, useEffect, useId, useCallback} from "react";
import Container from 'react-bootstrap/Container';
import logo from '../assets/img/logo.webp';
import top from '../assets/img/top.png';

export default function Footer() {
    const [toTop, setToTop] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            setToTop(window.scrollY >= 200)
        }
        window.addEventListener('scroll', handleScroll)

        return () => {
            window.removeEventListener('scroll', handleScroll)
        }
    }, [])


    return (
        <>
            <footer id="footer" className="nhut_style">
                <Container>
                    <div className="footer_bot">
                        <div className="logo ">
                            <a href="/" className="navbar-brand">
                                <img src={logo} alt="BAO VIET"/>
                            </a>
                            <div className="txt">BẢO HIỂM NGƯỜI VIỆT</div>
                        </div>
                        <div className="copyright">
                            <ul className="menu-terms">
                                <li>
                                    <a href="/dieu-khoan-dieu-kien" className="navbar-brand left">
                                        <span>Điều khoản điều kiện</span>
                                    </a>
                                </li>
                                <li>
                                    <a href="/chinh-sach-quyen-rieng-tu" className="navbar-brand right">
                                        <span>Chính sách quyền riêng tư</span>
                                    </a>
                                </li>
                            </ul>
                            <div className="terms-copy">
                                Copyright © 2021 by Bao Hiem Nguoi Viet. All Rights Reserved.<br/>
                                <span>Liên hệ với FanPage <a href="#!"
                                                             target="_blank">Bảo hiểm Người Việt</a> để hủy đăng ký nếu cần.</span>
                            </div>
                        </div>
                        <div className="social d-lg-flex flex-row pc">
                            <a href="https://www.facebook.com/bhnguoiviet">
                                <img className="pc"
                                     src={`${window.location.href}/img/facebook.webp`}
                                     alt="BAO VIET"/>
                            </a>
                        </div>
                    </div>

                </Container>

            </footer>

            {toTop && (
                <div style={{cursor:"pointer"}} className='back_top back_hide' onClick={()=>{
                    window.scrollTo({
                        top: 0,
                        behavior: "smooth"
                    });
                }}><img src={top} alt="top"/></div>
            )}

        </>
    )
}