import Slider from "react-slick";
import VideoYTBCard from "../VideoYTBCard";
import React, {useRef} from "react";

const data =
    [
        {
            "id": "985939",
            "title": "Bảo Việt An Gia | Bảo Hiểm Sức Khỏe Cho Chính Bạn",
            "image": require("../../assets/img/test1.jpg"),
            "url": "https://www.youtube.com/watch?v=kqvVFVvW1nE"
        },
        {
            "id": "532639",
            "title": "Bảo Việt An Gia | Bảo Hiểm Sức Khỏe Cho Trẻ Em",
            "image": require("../../assets/img/test11.jpg"),
            "url": "https://www.youtube.com/watch?v=0EPCA0-olks"
        },
        {
            "id": "760161",
            "title": "Bảo Việt An Gia | Bảo Hiểm Sức Khỏe Cho Người Lớn Tuổi",
            "image": require("../../assets/img/test111.jpg"),
            "url": "https://www.youtube.com/watch?v=ICeAdoM6VlY"
        }
    ];

export default function SimpleSlider(proj) {
    const customeSlider = useRef();

    const gotoNext = () => {
        customeSlider.current.slickNext()


    }
    const gotoPrev = () => {
        customeSlider.current.slickPrev()

    }
    const SlickArrowLeft = () => (
        <div  onClick={gotoPrev} className="slick-prev slick-arrow"><i
            className="fas fa-chevron-left"></i></div>
    );


    const SlickArrowRight = () => (
        <div onClick={gotoNext} className="slick-next slick-arrow"><i
            className="fas fa-chevron-right"></i></div>
    );

    const settings = {
        dots: true,
        speed: 500,
        slidesToShow: proj.isMobile ? 1 : 3,
        slidesToScroll: 1,
        initialSlide: 0,
        autoplay: false,
        arrows: true,
        prevArrow: <SlickArrowLeft/>,
        nextArrow: <SlickArrowRight/>,

    };


    return (

        <Slider ref={customeSlider}
            className="section_moi_slider video_slider style_dot style_arrow slick-slider slick-dotted" {...settings}>
            {
                data.map((items, index) => {
                    return (<VideoYTBCard key={index} url={items.url} image={items.image} caption={items.title}/>)
                })
            }
        </Slider>
    )
}