import React, {useRef} from "react";
import Slider from "react-slick";
import b1 from "../assets/img/1.png"
import b2 from "../assets/img/2.png"
import b3 from "../assets/img/3.png"
import b4 from "../assets/img/4.png"

function ItemBox(proj) {
    return (
        <div className="item">
            <div className="box">
                <img src={proj.image} alt=""/>
                <span>{proj.name}</span>
                <div className="txt_1">
                    {proj.keyword}
                </div>
            </div>
        </div>
    )
}

export default function ThuTuc () {

    const customeSlider = useRef();

    const gotoNext = () => {
        customeSlider.current.slickNext()


    }
    const gotoPrev = () => {
        customeSlider.current.slickPrev()

    }
    const SlickArrowLeft = () => (
        <div  onClick={gotoPrev} className="slick-prev slick-arrow"><i
            className="fas fa-chevron-left"></i></div>
    );


    const SlickArrowRight = () => (
        <div onClick={gotoNext} className="slick-next slick-arrow"><i
            className="fas fa-chevron-right"></i></div>
    );

    const settings = {
        dots: true,
        speed: 500,
        slidesToShow: 2,
        slidesToScroll: 1,
        initialSlide: 0,
        autoplay: false,
        arrows: false,
    /*    prevArrow: <SlickArrowLeft/>,
        nextArrow: <SlickArrowRight/>,*/

    };

    return (

        <Slider {...settings} className="rules_dt style_dot style_arrow d-flex flex-row justify-content-center slick-slider slick-dotted" ref={customeSlider} >
            <ItemBox
                image={b1}
                name="200+"
                keyword="Bệnh viện chất lượng cao trên cả nước"
            />
            <ItemBox
                image={b2}
                name="24/7"
                keyword="Dịch vụ bảo lãnh viện phí ưu việt"
            />
            <ItemBox
                image={b3}
                name="0"
                keyword="Không yêu cầu khám sức khỏe trước khi đăng ký"
            />
            <ItemBox
                image={b4}
                name="0"
                keyword="Không mất thời gian thu thập hồ sơ yêu cầu bồi thường"
            />
        </Slider>
    )
}