import React, {useState} from "react";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Forms from '../components/Forms';
import banner from '../assets/img/banner.png';
import bg from '../assets/img/bg_banner.png';
import bg2 from '../assets/img/bg_bieu-phi.png';
import bg3 from '../assets/img/test2.jpg';
import bg33 from '../assets/img/test2_mb.jpg';
import bg4 from '../assets/img/test3.png';
import bg5 from '../assets/img/bg_banner_mb.png';
import {Container} from "react-bootstrap";
import SimpleSlider from "../components/SimpleSlider";
import QlSlider from "../components/QlSlider";
import BpSlider from "../components/BpSlider";
import ProcedureSlider from "../components/ProcedureSlider";
import MediaQuery, {useMediaQuery} from 'react-responsive'
import ThuTuc from "../components/ThuTuc";
import HoiDap from "../components/HoiDap";
import b1 from "../assets/img/1.png"
import b2 from "../assets/img/2.png"
import b3 from "../assets/img/3.png"
import b4 from "../assets/img/4.png"
import {Tabs, Tab, Panel} from '@bumaga/tabs'


function ItemBox(proj) {
    return (
        <div className="item">
            <div className="box">
                <img src={proj.image} alt=""/>
                <span>{proj.name}</span>
                <div className="txt_1">
                    {proj.keyword}
                </div>
            </div>
        </div>
    )
}

function ItemList(proj) {
    return (
        <div className="d-flex flex-row align-items-center">
            <div className="txt-num">{proj.name}</div>
            <div className="txt-name ml-3">
                <div className="title_txt">{proj.description}</div>
                <div className="desc_txt">{proj.content}</div>
            </div>
        </div>
    )
}

function QuyTrinhList(proj) {
    return (
        <div className="item">
            <div className="icon">
                <img src={proj.image} alt="BAO VIET"/>
                <div className="step">
                    {proj.name}
                </div>
            </div>
            <div className="steps">
                <div className="desc">
                    {proj.keyword}
                </div>
            </div>
        </div>

    )
}

function Trangchu() {
    const [tabIndex, setTabIndex] = useState(0);
    const tabIndexData = [
        'Quy trình bảo lãnh',
        'Quy trình bồi thường',
    ]
    const currentUrl = window.location.href;
    const quytrinhData = [
        {image:`${currentUrl}/img/bb1.png`, name:'Bước 1', keyword:'Xuất trình CMND và thẻ BH.'},
        {image:`${currentUrl}/img/bb2.png`, name:'Bước 2', keyword:'Tiến hành khám chữa bệnh'},
        {image:`${currentUrl}/img/bb3.png`, name:'Bước 3', keyword:'Ký giấy bảo lãnh / nhận lại tiền đặt cọc nếu có/ thanh toán các chi phí không được chi trả.'},
    ]
    const quytrinhboithuongData = [
        {image:`${currentUrl}/img/1.png`, name:'Bước 1', keyword:'Khám, nhập viện / điều trị theo chỉ định của bác sĩ.'},
        {image:`${currentUrl}/img/2.png`, name:'Bước 2', keyword:'Thanh toán các chi phí cho bệnh viện. Lấy các chứng từ y tế, hóa đơn.'},
        {image:`${currentUrl}/img/3.png`, name:'Bước 3', keyword:'Gởi hồ sơ đến Bảo Việt (trong 60 ngày sau ngày điều trị cuối cùng/ tử vong).'},
        {image:`${currentUrl}/img/4.png`, name:'Bước 4', keyword:'Bảo Việt tiếp nhận hồ sơ bồi thường.'},
        {image:`${currentUrl}/img/5.png`, name:'Bước 5', keyword:'Thông báo về tình trạng hồ sơ bồi thường (3 ngày làm việc sau khi nhận đủ hồ sơ).'},
        {image:`${currentUrl}/img/6.png`, name:'Bước 6', keyword:'Bảo Việt hoàn tất thẩm định hồ sơ và hoàn trả kết quả trong vòng 15 ngày.'},
    ]
    const isMobile = useMediaQuery({query: '(max-width: 991px)'})
    return (
        <>
            <div className="hero_banner"
                 style={{
                     backgroundImage: `url(${isMobile ? bg5 : bg})`,
                 }}
            >
                <div className="nhut_style" id="dang-ky-tu-van">
                    <Container className="sec-container">
                        <Row className="wp no-gutters">
                            <div className="col-custom-left">
                                <div className="box-card">
                                    <img className="pc"
                                         src={banner}/>
                                    <img className="mb"
                                         src={banner}/>
                                </div>
                            </div>
                            <div className="col-custom-right">
                                <div className="wp_form">
                                    <div className="h5 wap_form_title">Nhập thông tin để được tư vấn miễn phí</div>
                                    <div className="fs-14 text-center mb-2"><span
                                        className="text-danger">Lưu ý:</span> Trẻ em dưới 6 tuổi cần mua kèm 1 người lớn
                                    </div>
                                    <Forms/>
                                </div>
                            </div>
                        </Row>
                    </Container>

                </div>
            </div>

            <div id="gioi-thieu" className="sec section_moi nhut_style">
                <Container>
                    <div className="div_title">
                        <h4 className="title_tt">Bảo hiểm sức khỏe Bảo Việt An Gia<p>An tâm tận hưởng cuộc sống</p></h4>
                        <div className="title_txt">
                            <p>Cuộc sống luôn tràn ngập những điều bất ngờ. Chủ động bảo vệ sức khỏe với bảo hiểm Bảo
                                Việt An Gia chính là giải pháp giúp tối ưu quyền lợi y tế, mang đến sự an tâm vui sống
                                cho bạn và gia đình.</p></div>
                    </div>
                    <div className="div_video">
                        <SimpleSlider isMobile={isMobile}/>
                    </div>
                </Container>
            </div>

            <div id="quyen-loi" className="sec sec_ql">
                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className="title_tt">Quyền lợi</h4>
                            <div className="title_txt">
                                <p>Dành cho mọi công dân và người nước ngoài từ 15 ngày tuổi đến 60 tuổi.</p></div>
                        </div>
                        <div className="div_quyenloi">
                            <div className="ql_slider style_dot style_arrow ql_no_slider ql_no_slider_center">
                                <QlSlider></QlSlider>
                            </div>
                        </div>
                    </Container>
                </div>
            </div>

            <div id="bieu-phi" className="sec sec_bp"
                 style={{
                     backgroundImage: "url(" + bg2 + ")",
                 }}
            >

                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className=" title_tt">Biểu phí bảo hiểm</h4>
                        </div>
                        <div className="div_bieuphi">

                            <BpSlider isMobile={isMobile}/>

                        </div>
                    </Container>

                    <Container className="con-promo">
                        <div className="ql_promo"
                             style={{
                                 '--bs-bg-m': `url(${isMobile ? bg33 : bg3})`,
                                 '--bs-bg': `url(${isMobile ? bg33 : bg3})`
                             }}
                        >
                            <div className="ql_tuvan">
                                <div className="ql_tuvan_div">
                                    <div className="ql_tuvan_tt">Ưu đãi đặc biệt</div>
                                    <div className="ql_tuvan_txt"><p>Khi mua theo nhóm</p></div>
                                    <a className="btn_web" href="#!"><span>Đăng ký tư vấn - Miễn phí!</span>
                                        &nbsp;
                                        <i className="fas fa-arrow-right"></i>
                                    </a>

                                </div>
                            </div>
                        </div>
                    </Container>
                </div>


            </div>

            <div id="uu-diem" className="sec sec_rules"
                 style={{
                     '--bs-bg-m': `url(${bg4})`,
                     '--bs-bg': `url(${bg4})`
                 }}
            >
                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className="title_tt">Thủ tục nhanh chóng</h4>
                        </div>
                        <div className="div_rules">
                            {isMobile ? (
                                <ThuTuc/>
                            ) : (
                                <div className="rules_dt style_dot style_arrow d-flex flex-row justify-content-center">
                                    <ItemBox
                                        image={b1}
                                        name="200+"
                                        keyword="Bệnh viện chất lượng cao trên cả nước"
                                    />
                                    <ItemBox
                                        image={b2}
                                        name="24/7"
                                        keyword="Dịch vụ bảo lãnh viện phí ưu việt"
                                    />
                                    <ItemBox
                                        image={b3}
                                        name="0"
                                        keyword="Không yêu cầu khám sức khỏe trước khi đăng ký"
                                    />
                                    <ItemBox
                                        image={b4}
                                        name="0"
                                        keyword="Không mất thời gian thu thập hồ sơ yêu cầu bồi thường"
                                    />
                                </div>
                            )}
                            <div className="wp-btn-tuvan">
                                <a className="btn_web btn_center txt_1" href="#!"><span>Tư vấn miễn phí</span></a>
                            </div>
                        </div>
                    </Container>
                </div>
            </div>

            <div id="hoi-dap" className="sec sec_tai_sao">
                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className="title_tt">Tại sao chọn Bảo Việt An Gia?</h4>
                        </div>
                        <div className="div_taisao">
                            {isMobile ? (
                                <HoiDap/>
                            ) : (
                                <div className="wp d-lg-flex flex-row justify-content-center align-items-center">
                                    <div className="wp-form left">
                                        <ItemList
                                            name="1"
                                            description="Đảm bảo tài chính vững vàng trước rủi ro."
                                            content="Giảm bớt áp lực tài chính khi biến cố xảy ra, giúp bạn an tâm điều trị."
                                        />
                                        <ItemList
                                            name="2"
                                            description="Dịch vụ bảo lãnh viện phí rộng khắp Việt Nam."
                                            content="Tùy chọn bệnh viện tư nhân và quốc tế trên toàn quốc mà bạn tin tưởng."
                                        />
                                        <ItemList
                                            name="3"
                                            description="Mua nhanh đơn giản, không cần khám sức khỏe."
                                            content="Đăng ký nhanh gọn chỉ trong vòng 5 phút với phí chỉ từ 3.000 đồng/ngày."
                                        />
                                    </div>
                                    <div className="wp-form right">
                                        <div className="procedure_slider">
                                            <div data-slider="1"
                                                 className="procedure_for style_arrow slick-initialized slick-slider slick-dotted">
                                                <ProcedureSlider></ProcedureSlider>
                                            </div>
                                            <div className="procedure_nav">

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                            }
                        </div>
                    </Container>
                </div>
            </div>
            <div id="quy-trinh" className="sec sec_quy_trinh">
                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className="title_tt">Quy trình đơn giản</h4>

                        </div>
                        <div className="div_quytrinh">
                            <Tabs>
                                <div className="div_fixedElement">
                                    <ul className="fixedElement nav nav-tabs quick_tabs_btn btn_mask procedure_btn justify-content-center d-flex flex-wrap">
                                        {tabIndexData.map((item, index) =>
                                            <Tab>
                                                <li className="nav-item">
                                                    <a onClick={() => setTabIndex(index)}
                                                       className={`nav-link ${tabIndex == index ? 'active' : ''}`}>{item}</a>
                                                </li>
                                            </Tab>)}
                                    </ul>
                                </div>

                                <Panel>
                                    <div className="procedure_note">
                                        Bảo Việt sẽ thanh toán chi phí theo nội dung bảo hiểm, người được bảo hiểm
                                        không cần thanh toán. Quy trình bảo lãnh như sau (Khi khám/ điều trị trong
                                        hệ thống bảo lãnh):
                                    </div>

                                    <div
                                        className="procedure_slider_list section_moi_slider style_dot style_arrow">
                                        {
                                            quytrinhData.map((data, index)=>{
                                                return (
                                                    <QuyTrinhList
                                                        image={data.image}
                                                        name={data.name}
                                                        keyword={data.keyword}
                                                    />
                                                )
                                            })
                                        }
                                    </div>
                                </Panel>
                                <Panel>
                                    <div className="procedure_note">
                                        Người được bảo hiểm thanh toán trước, Bảo Việt thanh toán chi phí thuộc nội
                                        dung bảo hiểm sau. Quy trình bồi thường như sau (Khi khám/ điều trị ngoài hệ
                                        thống bảo lãnh):
                                    </div>
                                    <div className="procedure_slider_list">
                                        {
                                            quytrinhboithuongData.map((data, index)=>{
                                                return (
                                                    <QuyTrinhList
                                                        image={data.image}
                                                        name={data.name}
                                                        keyword={data.keyword}
                                                    />
                                                )
                                            })
                                        }
                                    </div>
                                </Panel>
                            </Tabs>

                        </div>
                    </Container>
                </div>
            </div>


        </>


    )
}


export default Trangchu;