import React from 'react';
import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { styled } from '@mui/material/styles';
import { Fancybox } from "@fancyapps/ui";
import "@fancyapps/ui/dist/fancybox/fancybox.css";
import ButtonPlay from "../../assets/img/icon_play.svg";


const VideoYTBCard = ({image, caption, url}) => {

    Fancybox.bind("[data-fancybox]", {
        // Your custom options
    });

    return (
        <div className="item">
          <div className="wap_item">
              <a href={url} className="link_video" data-fancybox
                 data-caption={caption}
                 data-src={url}
              >
                  <span className="btn_play"
                  style={{ backgroundImage: `url(${ButtonPlay})`}}
                  ></span>

                  <img className="img_item"  src={image} />
                  <span className="card-body">
                      <p className="card-text"></p>
                  </span>
              </a>
              <a data-fancybox href="#!" className="link_title">
                  {caption}
              </a>
          </div>
        </div>
    )
}


export default VideoYTBCard;