function QlItems(items) {
    items = items.items;

    return (
        <div className="item">
            <div className="slider_item">
                <div className="item_img">
                    <a href="#!">
                        <img className="img" src={items.image} alt=""/>
                    </a>
                </div>

                <div className="item_info">
                    <h3><a href="#!">{items.title}</a></h3>
                    <div className="title_txt">
                        {items.note}
                        <div>
                            <p><strong>{items.description}</strong>/người/năm</p>
                        </div>
                    </div>
                    <div className=" item_info_note">
                        {items.keyword}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default function QlSlider() {
    const SlickArrowLeft = ({currentSlide, slideCount, ...props}) => (
        /*<img src={LeftArrow} alt="prevArrow" {...props} />*/
        <div className="slick-prev"><FaArrowAltCircleLeft/></div>
    );

    const SlickArrowRight = ({currentSlide, slideCount, ...props}) => (
        // <img src={RightArrow} alt="nextArrow" {...props} />
        <div className="slick-next"><FaArrowAltCircleRight/></div>
    );

    const settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 1,
        initialSlide: 0,
        autoplay: false,
        arrows: true,
        prevArrow: <SlickArrowLeft/>,
        nextArrow: <SlickArrowRight/>,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };


    return (
        data.map((items, index) => {
            return <QlItems items={items} key={index} />
        })
    )
}